* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

body {
	font-family: $f-afacad;
	font-size: 16px;
	line-height: 1.4;
	overflow-x: hidden;
	color: #000;

	&.admin-bar {
		#wcl-main-header {
			top: 32px !important;

			@media only screen and (max-width: 782px) {
				top: 46px !important;
			}

			@media only screen and (max-width: 600px) {
				top: 0 !important;
			}
		}
	}
}

html.overflow-hidden,
body.overflow-hidden {
	overflow: hidden;
}

img,
svg {
	max-width: 100%;
	height: auto;
}

iframe {
	max-width: 100%;
}

p,
ul,
ol {
	margin-bottom: 20px;
}

p:last-child,
ul:last-child,
ol:last-child {
	margin-bottom: 0;
}

input,
textarea {
	border-radius: 0; // Prevent IOS add border-radius by default to all input elements
}

a {
	-webkit-transition: all .25s ease-in-out;
	-o-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
}

span {
	display: inline-block;
}

.wcl-body-inner {
	// display: -webkit-box;
	// display: -ms-flexbox;
	// display: flex;
	// min-height: 100vh;
	// overflow: hidden;
	// -webkit-box-orient: vertical;
	// -webkit-box-direction: normal;
	// -ms-flex-direction: column;
	// flex-direction: column;
	// justify-content: space-between
}

.wcl-section {
	position: relative;
}

.wcl-container {
	width: 100%;
	max-width: 1920px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 120px;
	padding-right: 120px;

	@media only screen and (max-width: 1199px) {
		padding-left: 50px;
		padding-right: 50px;
	}

	@media only screen and (max-width: 991px) {
		padding-left: 25px;
		padding-right: 25px;
	}

	@media only screen and (max-width: 767px) {
		padding-left: 20px;
		padding-right: 20px;
	}
}


@media (max-width: 280px) {
	html {
		min-width: 280px;
		overflow-x: auto;
	}
}







