}

ul,
ol {
	padding-left: 20px;
}




/** Form */
.wcl-form {
	position: relative;
}

.wcl-form.loading .wcl-preloader {
	display: block;
}

.wcl-form.loading>*:not(.wcl-preloader) {
	opacity: 0.5;
	pointer-events: none;
}

.wcl-form .wcl-message {
	width: 100%;
	margin: 20px 0;
	text-align: center;
}

.wcl-form .wcl-preloader {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: none;
	width: 100px;
}

.wcl-message {
	display: none;
	font-size: 14px;
}

.wcl-message.show {
	display: block;
}

.wcl-message.error {
	// Change the color to the desired one
	color: red;
}

.wcl-message.success {
	// Change the color to the desired one
	color: green;
}





/** CF7 */
.wpcf7 form .wpcf7-response-output {
	margin: 1em 0;
	padding: 0;
	color: #00a0d2;
	border: none;
}

.wpcf7 form.sent .wpcf7-response-output {
	color: #46b450;
}

.wpcf7 form.failed .wpcf7-response-output,
.wpcf7 form.aborted .wpcf7-response-output {
	color: #dc3232;
}

.wpcf7 form.spam .wpcf7-response-output {
	color: #f56e28;
}

.wpcf7 form.invalid .wpcf7-response-output,
.wpcf7 form.unaccepted .wpcf7-response-output,
.wpcf7 form.payment-required .wpcf7-response-output {
	color: #dc3232;
	border: none;
}

.wpcf7-not-valid-tip {
	font-size: .8em;
}

